<template>
  <div>
    <myHeader></myHeader>
    <img src="../../../static/img/beijing3.png" style="padding-top:85px;width: 100% ">
    <div class="page_content">
      <div class="page_content_left">
        <div style="background-color: #4c9cff;font-size: 18px;color:#ffffff">
          /
          <span>Information Center</span>
        </div>
        <div @click="pageTurn('/infoCenter/policyLow')"></div>
        <div
          @click="pageTurn('/infoCenter/videoNews')"
          style="background-color: #b1cbff;color:#ffffff"
        ></div>
        <div @click="pageTurn('/infoCenter/clubActivity')"></div>
      </div>

      <div class="page_content_right" v-show="allShow">
        <p class="page_content_right_title"></p>
        <div class="page_content_right_items">
          <div class="page_content_right_item" v-for="item in videoNews">
            <video width="250px" height="140px" controls>
              <source :src="item.src" type="video/mp4">
            </video>
            <p @click="detailVideo(item.text,item.src)">{{item.text }}</p>
          </div>
        </div>
        <el-pagination
          class="paginationStyle"
          background
          layout="prev,pager,next,jumper,->,total, slot"
          prev-text=""
          next-text=""
          :page-size="3"
          :total="50"
        ></el-pagination>
      </div>
      <div class="page_content_right" v-show="!allShow">
        <p class="page_content_right_title">
          <a @click="smallVideo()"></a>
          >>{{detailText}}
        </p>
        <video
          controls
          style="padding:20px;width:810px;height: 580px"
          v-for="item in detailSrc"
          :key="item"
        >
          <source :src="item" type="video/mp4">
        </video>
      </div>
    </div>
    <myFooter></myFooter>
  </div>
</template>

<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";

export default {
  name: "videoNews",
  data() {
    return {
      allShow: true,
      detailSrc: [],
      detailText: "",
      videoNews: [
        {
          src: " ./static/video/video1.mp4",
          text: "CCTV ..."
        },
        {
          src: " ./static/video/video2.mp4",
          text: "[] ..."
        },
        {
          src: " ./static/video/video1.mp4",
          text: " ..."
        },
        {
          src: " ./static/video/video1.mp4",
          text: "CCTV ..."
        },
        {
          src: " ./static/video/video1.mp4",
          text: "[] ..."
        },
        {
          src: " ./static/video/video1.mp4",
          text: " ..."
        },
        {
          src: " ./static/video/video1.mp4",
          text: "CCTV ..."
        },
        {
          src: " ./static/video/video1.mp4",
          text: "[] ..."
        },
        {
          src: " ./static/video/video1.mp4",
          text: " ..."
        }
      ]
    };
  },
  methods: {
    detailVideo: function(text, src) {
      this.allShow = false;
      console.log(src);

      this.detailSrc.push(src);
      this.detailText = text;
    },
    smallVideo: function() {
      this.allShow = true;
      this.detailSrc = [];
    },
    pageTurn: function(path) {
      this.$router.push({ path: path });
    }
  },
  components: {
    myHeader: Header,
    myFooter: Footer
  }
};
</script>

<style scoped>
.page_content {
  display: flex;
  width: 1200px;
  height: 1000px;
  margin: 0 0 0 calc(50% - 600px);
}

.page_content_left {
  margin-top: 60px;
  height: 204px;
  width: 300px;
  border-bottom: solid 1px #b3cdff;
  border-left: solid 1px #b3cdff;
  border-right: solid 1px #b3cdff;
  border-radius: 5px;
  overflow: hidden;
}

.page_content_left > div {
  padding-left: 48px;
  height: 50px;
  line-height: 50px;
  border-top: solid 1px #b3cdff;
  color: #b1cbff;
  letter-spacing: 1px;
  cursor: pointer;
}

.page_content_left > div > span {
  font-size: 12px;
}

.page_content_right {
  padding-left: 38px;
  padding-top: 119px;
  width: 850px;
}

.page_content_right_title {
  padding-bottom: 20px;
  border-bottom: 1px solid #4c9cff;
  font-size: 16px;
  letter-spacing: 1px;
  color: #60646c;
}

.page_content_right_items {
  display: flex;
  flex-wrap: wrap;
}

.page_content_right_item {
  padding: 20px 10px 0 10px;
  width: 260px;
  height: 190px;
}

.page_content_right_item > p {
  margin-top: 10px;
  font-family: MicrosoftYaHei;
  font-size: 16px;
  color: #999999;
}

.paginationStyle {
  padding: 45px 50px 45px 150px;
}
</style>



